@use "global" as *;

.p-journal {
    margin-top: rem(60);
    position: relative;
    background-color: #fff;
}

.p-journal--belongings {
    margin-top: 0;
    background-color: initial

}

.p-journal__inner {
    padding-top: rem(60);
    padding-bottom: rem(60);

    @include mq(md) {
        max-width: initial;
        padding-bottom: rem(140);
    }
}

.p-journal__inner--belongings {
    padding-bottom: rem(89);

    @include mq(md) {
        padding-bottom: rem(140);
    }
}

.p-journal__container {
    @include mq(md) {
        display: flex;
        // justify-content: space-between;
        max-width: initial;
    }
}

.p-journal__title {
    font-family: $en-font-first;
    font-size: rem(56);
    font-weight: 700;
    writing-mode: vertical-rl; /* 縦書き向きを指定 */
    position: absolute;
    content: "";
    left: rem(-50);
    top: rem(-20);
    transform: rotate(180deg);
    letter-spacing: -0.024em;
    margin-left: rem(-20);
    line-height: 1;

    @include mq(md) {
        position: relative;
        text-align: end;
        left: initial;
        top: initial;
        width: 16.6%;
        max-width: rem(210);
        min-width: rem(210);
        font-size: rem(70);
        margin-left: rem(-10);
    }
}

.p-journal__title--belongings {
    font-family: $en-belongings-Malaga;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: .02em;
    top: rem(-75);
    left: rem(-58);

    @include mq(md) {
        top: initial;
        left: initial;
    }
}
.p-journal__contents {
    width: 85%;
    margin-left: auto;

    @include mq(md) {
        width: calc(100% - #{rem(240)});
        margin-left: rem(52);
    }
}

.p-journal__comment {
    font-family: $en-font-second;
}

.p-journal__box {
    display: flex;
    position: relative;
}

.p-journal__box--belongings {
    display: block;
}

.p-journal__cards {
    margin-top: rem(55);
    width: 100%;

    @include mq(md) {
        display: flex;
        justify-content: center;
        margin-top: rem(60);
    }
}

.p-journal__cards--belongings {
    @include mq(md) {
        display: block;
    }
}

.p-journal__card:not(:first-child) {
    margin-top: rem(40);

    @include mq(md) {
        margin-top: 0;
        margin-left: rem(15);
    }
}

.p-journal__image {
    position: relative;
    overflow: hidden;
}

.p-journal__image img {
    aspect-ratio: 1;
    object-fit: cover;
    transition: all 0.3s;
}

.p-journal__image--belongings img {
    @include mq(md) {
        aspect-ratio: 950/465;
        object-fit: cover;
        transition: all 0.3s;
        
    }
}

.p-journal__imageTitle {
    color: $red;
    font-weight: 400;
    font-family: $en-font-first;
    font-size: rem(45);
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include mq(md) {
        font-size: rem(75);
    }
}

.p-journal__small {
    font-size: rem(14);
    font-family: $en-font-second;
    font-weight: 600;
    margin-top: rem(17);

    @include mq(md) {
        font-size: rem(15);
        margin-top: rem(23);
    }
}

.p-journal__small--belongings {
    font-weight: 700;
    line-height: normal;
    font-family: $en-belongings-Malaga;
    margin-bottom: rem(12);
}

.p-journal__middle {
    font-size: rem(18);
    font-family: $en-font-second;
    font-weight: 500;
    // text-decoration: underline;
    width: fit-content;
    margin-top: rem(5);
    // position: relative;
    
    background-size: 100% rem(2);/*幅をゼロにしておく*/
    background-position: 0 82%;
    background-repeat: no-repeat;
    transition: .8s;
    display: inline;


    @include mq(md) {
        font-size: rem(22);
        margin-top: rem(10);
        background-size: 0% rem(2);/*幅をゼロにしておく*/
    }
}

.p-journal__middle--belongings {
    
    font-family: $en-belongings-lapture;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background-position: 0 97%;
}


.p-journal__text--red {
    color: $red;
}

.p-journal__text--green {
    color: $green;
}

.p-journal__text--black {
    color: $black;
}

.p-journal__middle--red {
    background-image: linear-gradient($red, $red);
    color: $red;
}

.p-journal__middle--green {
    background-image: linear-gradient($green, $green);
    color: $green;
}

.p-journal__middle--black {
    background-image: linear-gradient($black, $black);
    color: $black;
}

// マウスオーバー
.p-journal__link:hover .p-journal__image img {
    @include mq(md) {
        transform: scale(1.05);
        transition: 0.3s all;
    }
}

.p-journal__link:hover .p-journal__middle {
    @include mq(md) {
        background-size: 100% rem(2);/*幅をゼロにしておく*/
    }
}
