@use "global" as *;

.m-about {
    margin-top: rem(50);

    @include mq(md) {
        display: flex;
        padding-left: rem(40);
        padding-right: rem(40);
        margin-top: rem(150);
    }
}

.m-about__image {
    @include mq(md) {
        width: 50%;
        padding-right: rem(48);
    }
}

.m-about__text {
    margin-top: rem(50)!important;

    @include mq(md) {
        margin-top: rem(0)!important;
    }
}


